.inp {
  margin: 1rem;
}
form {
  border: 0.3rem groove azure;
  padding: 1rem;
  display: flex;
  flex-direction: column;
}
.addBtn {
  padding: 1rem;
  background-color: rgba(255, 255, 255, 0);
  border: none;
}
.addBtn svg {
  scale: 2;
  color: rgb(36, 35, 34);
  cursor: pointer;
}
